import React, { useState } from 'react';
import { BrowserRouter as Router, Routes, Route } from 'react-router-dom';
import { DndProvider } from 'react-dnd';
import { HTML5Backend } from 'react-dnd-html5-backend';
import { ConnectionProvider } from './context/ConnectionContext';
import ConnectionStatus from './components/ConnectionStatus';
import SelectionScreen from './components/SelectionScreen';
import SubjectScreen from './components/SubjectScreen';
import ResearcherScreen from './components/ResearcherScreen';
import './App.css';

function App() {
  const [dndError, setDndError] = useState(null);

  if (dndError) {
    return (
      <div className="app-error">
        <h2> </h2>
        <p>{dndError.message}</p>
        <button onClick={() => window.location.reload()}> </button>
      </div>
    );
  }

  return (
    <DndProvider backend={HTML5Backend}>
      <ConnectionProvider>
        <Router>
          <ConnectionStatus />
          <Routes>
            <Route path="/" element={<SelectionScreen />} />
            <Route path="/subject" element={<SubjectScreen />} />
            <Route path="/researcher" element={<ResearcherScreen />} />
          </Routes>
        </Router>
      </ConnectionProvider>
    </DndProvider>
  );
}

export default App; 