import React, { useState, useEffect } from 'react';
import './SubjectsManagement.css';

const SubjectsManagement = () => {
  const [subjects, setSubjects] = useState([]);

  useEffect(() => {
    fetchSubjects();
  }, []);

  const fetchSubjects = async () => {
    try {
      const response = await fetch('http://localhost:5000/api/subjects');
      const data = await response.json();
      setSubjects(data);
    } catch (error) {
      console.error('Error fetching subjects:', error);
    }
  };

  const getStatusIcon = (status) => {
    switch (status) {
      case 'waiting':
        return 'clock';
      case 'in_progress':
        return 'running';
      case 'completed':
        return 'check-circle';
      case 'construction':
        return 'tools';
      default:
        return 'question-circle';
    }
  };

  const getStatusText = (status) => {
    switch (status) {
      case 'waiting':
        return ' ';
      case 'in_progress':
        return '  ';
      case 'completed':
        return ' ';
      case 'construction':
        return ' ';
      default:
        return ' ';
    }
  };

  const handleStartSession = async (subjectId) => {
    try {
      await fetch(`http://localhost:5000/api/sessions/start/${subjectId}`, {
        method: 'POST'
      });
      fetchSubjects(); //    
    } catch (error) {
      console.error('Error starting session:', error);
    }
  };

  const handleStopSession = async (subjectId) => {
    try {
      await fetch(`http://localhost:5000/api/sessions/stop/${subjectId}`, {
        method: 'POST'
      });
      fetchSubjects();
    } catch (error) {
      console.error('Error stopping session:', error);
    }
  };

  const handleRestartSession = async (subjectId) => {
    try {
      await fetch(`http://localhost:5000/api/sessions/restart/${subjectId}`, {
        method: 'POST'
      });
      fetchSubjects();
    } catch (error) {
      console.error('Error restarting session:', error);
    }
  };

  return (
    <div className="subjects-management">
      <h2> </h2>
      <div className="subjects-grid">
        {subjects.map(subject => (
          <div key={subject.id} className="subject-card">
            <div className="subject-info">
              <i className="fas fa-user-circle avatar"></i>
              <div className="subject-details">
                <h3>{subject.fullName}</h3>
                <div className={`subject-status ${subject.status}`}>
                  <i className={`fas fa-${getStatusIcon(subject.status)}`}></i>
                  <span>{getStatusText(subject.status)}</span>
                </div>
              </div>
            </div>
            <div className="subject-actions">
              {subject.status === 'waiting' && (
                <button 
                  className="start-button"
                  onClick={() => handleStartSession(subject.id)}
                >
                  <i className="fas fa-play"></i>
                   
                </button>
              )}
              {subject.status === 'in_progress' && (
                <button 
                  className="stop-button"
                  onClick={() => handleStopSession(subject.id)}
                >
                  <i className="fas fa-stop"></i>
                   
                </button>
              )}
              {subject.status === 'completed' && (
                <button 
                  className="restart-button"
                  onClick={() => handleRestartSession(subject.id)}
                >
                  <i className="fas fa-redo"></i>
                   
                </button>
              )}
            </div>
          </div>
        ))}
      </div>
    </div>
  );
};

export default SubjectsManagement; 