import React, { useState, useRef } from 'react';
import { useDrag, useDrop } from 'react-dnd';
import BlockSelectionMenu from './BlockSelectionMenu';

function PuzzlePiece({ id, type, title, icon, isDraggable, onDragStart, onDragEnd, onRemove, index, onDrop, availableBlocks, isInserting }) {
  const [isRemoving, setIsRemoving] = useState(false);
  const [showMenu, setShowMenu] = useState(false);
  const [menuPosition, setMenuPosition] = useState({ top: 0, left: 0 });
  const blueSquareRef = useRef(null);

  const [{ isOver }, drop] = useDrop(() => ({
    accept: 'puzzle-piece',
    drop: (item) => {
      if (onDrop) {
        console.log('Dropping at blue square, index:', index);
        onDrop(index + 1, item, true);
      }
      return undefined;
    },
    collect: (monitor) => ({
      isOver: !!monitor.isOver() && !!monitor.canDrop()
    })
  }));

  const [{ isDragging }, drag] = useDrag(() => ({
    type: 'puzzle-piece',
    item: () => {
      if (onDragStart) onDragStart();
      return { id, type, title, icon };
    },
    end: () => {
      if (onDragEnd) onDragEnd();
    },
    collect: (monitor) => ({
      isDragging: monitor.isDragging()
    }),
    canDrag: () => isDraggable
  }));

  const handleDelete = (e) => {
    e.stopPropagation();
    e.preventDefault();
    setIsRemoving(true);
    setTimeout(() => {
      if (onRemove) onRemove();
    }, 300);
  };

  const handleBlueSquareClick = (e) => {
    e.stopPropagation();
    const rect = e.currentTarget.getBoundingClientRect();
    
    setMenuPosition({
      top: rect.top,
      left: rect.left - 250,
    });
    setShowMenu(true);
  };

  const handleBlockSelect = (block) => {
    if (onDrop) {
      onDrop(index + 1, block, true);
    }
  };

  drop(blueSquareRef);

  return (
    <div
      ref={drag}
      className={`puzzle-piece ${isDragging ? 'dragging' : ''}`}
      data-type={type}
      data-id={id}
      data-removing={isRemoving || undefined}
    >
      <div 
        ref={blueSquareRef}
        className={`blue-square ${isOver ? 'over' : ''}`}
        onClick={handleBlueSquareClick}
      />
      {showMenu && (
        <BlockSelectionMenu
          blocks={availableBlocks}
          onSelect={handleBlockSelect}
          position={menuPosition}
          onClose={() => setShowMenu(false)}
        />
      )}
      {onRemove && (
        <div className="delete-button" onClick={handleDelete}>
          
        </div>
      )}
      <div className="piece-icon">{icon}</div>
      <div className="piece-title">{title}</div>
    </div>
  );
}

export default PuzzlePiece; 